<template>
  <div class="wrapper"
    :class="canvasPositionClass"
    :style="{
      // overflow:
      backgroundColor:bgColor,backgroundImage:'url('+imgBase64+')',
      height:isMobileDevice?mobilePagesNum*100+'vh':dashboardHeight+'px',
      width:isMobileDevice?'100vw':dashboardWidth+'px'}">
    <!-- 数据加载动画 -->
    <loading-rotating
      v-if="isMobileDevice&&!dataIsOk"
      :styleObj="styleObj"></loading-rotating>
    <Loading :dataIsOk="dataIsOk"
      v-if="!isMobileDevice"
      :styleObj="styleObj"></Loading>
    <div v-for="(item,index) in getChartList"
      class="component-container"
      :key="index"
      :style="{
        // transform:
        // `translate(${!isMobileDevice?Number(item.containerOptions.xPosition):Number(item.containerOptions.xPositionMobile)}px,
        // ${!isMobileDevice?Number(item.containerOptions.yPosition):Number(item.containerOptions.yPositionMobile)}px`+')',
        width:!isMobileDevice?Number(item.containerOptions.tableWidth)+'px':Number(item.containerOptions.tableWidthMobile*screenWidth)+'px',
        height:!isMobileDevice?Number(item.containerOptions.tableHeight)+'px':Number(item.containerOptions.tableHeightMobile)+'px',
        left:(!isMobileDevice?Number(item.containerOptions.xPosition):Number(item.containerOptions.xPositionMobile))+'px',
        top:(!isMobileDevice?Number(item.containerOptions.yPosition):Number(item.containerOptions.yPositionMobile))+'px',
        position:isMobileLayoutSaved||!isMobileDevice?'absolute':'static',
        float:isMobileLayoutSaved?'usnet':'left',
        zIndex:Number(item.containerOptions.level)||0,}">
      <component :is="item.containerOptions.chartsType"
        :style="{
          'z-index':item.containerOptions.level+3||3,}"
        :chartIndex="index"
        :options="item.renderOptions"
        :containerOptions="item.containerOptions"
        :containerStatus="item.containerStatus"
        :isSetOptions="item.containerStatus.isSetOptions"></component>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import {mapGetters} from 'vuex';
import Loading from '@/components/Loading/LoadingProgress.vue';
import {asyncComponent} from './mixnin/asyncComponent';
import {chartDataFill} from '@/utils/chartRender';
import {beforeThemeHandle} from '@/utils/optionSetting';
import {store} from '@/store/store';
//echart主题
//异步按需加载组件

export default {
	name: 'PreviewPages',
	mixins: [asyncComponent],
	components: {
		Loading,
	},
	data() {
		return {
			isMobileLayoutSaved:false,
			isMobileDevice: false,
			mobilePagesNum: 1,
			shareType: 1,
			dataIsOk: false,
			recordTime: 1000 * 5,
			intervalId: 0,
			styleObj: {
				width: '100%',
				height: '100%'
			},
			// bgColor: '#172132',
			bgColor: '',
			imgBase64: '',
			dashboardWidth: '',
			dashboardHeight: '',
		};
	},
	computed: {
		canvasPositionClass() {
			if (this.isMobileDevice) return ['mobileLayout'];
			let classArr = [];
			if (this.screenWidth > this.dashboardWidth && this.screenHeight > this.dashboardHeight) {
				classArr.push('allCenter');
				return classArr;//画布宽高都小于页面时居中
			}
			if (this.screenWidth > this.dashboardWidth) {//画布宽度小于页面宽度
				classArr.push('aCenter');
			}
			if (this.screenHeight > this.dashboardHeight) {
				classArr.push('vCenter');
			} else {
				classArr.push('overflowY');
			}
			return classArr;
		},
		// nonTransform() {
		// 	return {transform: `scale(${1 / this.scaleLevel})`};
		// },
		...mapGetters('main', {
			screenWidth: 'getScreenWidth',
			screenHeight: 'getScreenHeight',
		}),
		...mapGetters('canvasPlace', {
			chartList: 'getChartList',
			// chartListFilter: 'chartMobileFilter',//移动端图表数据
			scaleLevel: 'getScaleLevel',
			renderedChartCount: 'getRenderedChartCount',
		}),
		...mapGetters('config', {
			baseUrl: 'baseUrl'
		}),
	  getChartList() {
		  if (this.isMobileDevice) {
			  return this.chartListFilter;
		  } else {
			  return this.chartList;
		  }
	  },
	  chartListFilter() {
		  return this.chartList.filter(item =>
			  item.containerOptions.showInMobileLayout && item.containerStatus.isSetOptions
		  );
	  }
	},
	watch: {
		dataIsOk(val) {
			if (val) document.dispatchEvent(new Event('render-active'));
		}
	},
	methods: {
	  // chartScheme(item) {
		//   return chartDataFill(item).chartOptions;
	  // },
		//激活推送Socket
		activateSocket(params) {
		  let vm=this;
			let server='wss://socketbi.24e.co/';
			this.ws=new WebSocket(server); //绑定到vue上
			this.ws.onopen = function(evt) {  //绑定连接事件
			};
			this.ws.onmessage = function(evt) {//绑定收到消息事件
				let data=JSON.parse(evt.data);
				if(data.connectFd){
					params.fd=data.connectFd;
					vm.axios.get('https://pushbi.24e.co',{params}) .then(res => {
					});
				}else{
					vm.$store.dispatch('canvasPlace/screeningRequest');//筛选器页面状态
				}

			};
		},
		closeSocket(){
		  this.ws.close();

		},
		getUrlParams(data,flag){
			let vm = this;
			const urlParams = {
				shareSn: vm.$route.query.share_sn,
				password: vm.$route.query.password,
				shareScreenData:data || []
			};
			const url = `${vm.baseUrl}api/shareInfo`;
			vm.axios.post(url,  urlParams)
				.then(res => {
					let resNew = res.data;
					console.log('resNew',resNew);
					// resNew.data.dashboardBgColor&&vm.bgColor = resNew.data.dashboardBgColor;
					console.log(resNew.data.dashboardBgColor);
					vm.bgColor = resNew.data.dashboardBgColor || '#0b0e14';
					vm.imgBase64 = resNew.data.dashboardBgImage;
					vm.dashboardHeight = resNew.data.dashboardHeight;
					vm.dashboardWidth = resNew.data.dashboardWidth;
					if (resNew.code == '000000') {
						if(resNew.data.pushInfo.pushState && !flag){  //为true开启推送
						  vm.activateSocket({product_sn:resNew.data.project_sn,time:resNew.data.pushInfo.pushTime});
						}
						vm.dataIsOk = true;
						document.title = resNew.data.project_name;
						vm.isMobileLayoutSaved=resNew.data.isMobileLayoutSaved;
						vm.shareType = resNew.data.share_type;
						vm.$store.commit('canvasPlace/clearArrChart');
						if (!vm.chartList.length) {
							let shareInfo = resNew.data.share_info;
							if (shareInfo.length) {
								shareInfo.forEach((item, idx) => {
									let containerOptions = item.containerOptions;
									let chartLargeType = item.containerOptions.chartsType.split('-')[0];
									if (vm.shareType == 2) {
										containerOptions.xPosition = 0;
										containerOptions.yPosition = 0;
									}
									let obj = {
										id: idx,
										containerOptions: containerOptions,
										containerStatus: {
											isSetOptions: true,
											dataDimensionShow: false,
											isDeleted: false,
											isShowLoading: false
										}
									};

									let renderOptions = beforeThemeHandle(item).renderOptions;
									vm.$set(obj, 'renderOptions', renderOptions);
									vm.$store.commit('canvasPlace/setChartList', obj);
								});
							} else {
								vm.dataIsOk = true;
							}
						}
					} else {
						vm.dataIsOk = true;
						vm.tips.show([resNew.err_msg]);
					}
				})
				.catch(error => {
					vm.dataIsOk = true;
					console.error(error);
				});
		},
		recordShareTime() {
			let vm = this;
			const url = `${vm.baseUrl}api/recordShare`;
			const share_sn = vm.$route.query.share_sn;
			const func=vm.$route.query.func || '';//后端判断是否截图的请求标识
			let timer=setInterval(() => {
				if(!func){

					vm.axios
						.get(`${url}?share_sn=${share_sn}`)
						.then(res => {
							if (res.data.code != '000000') {
								vm.tips.show([res.data.err_msg]);
							}
						})
						.catch(error => console.error(error));
				}else{
					clearInterval(timer);
				}
			}, 5000);
		}
	},
	beforeMount() {
		this.isMobileDevice = navigator.userAgent.toLowerCase().match(/(ipod|iphone|android|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince)/i) != null;
	},
	mounted() {
	  this.$on('sb',function (e) {
			console.log('hahahah',e);
		});
		let vm = this;
		vm.getUrlParams();
		vm.recordShareTime();
	  setTimeout(() => {
		  if (vm.isMobileDevice) {
			  document.querySelector('html').style.overflow = 'auto';
			  let array = vm.chartListFilter.map(ele => {
				  return Number(ele.containerOptions.yPositionMobile) + Number(ele.containerOptions.tableHeightMobile);
			  });
			  let maxBottom = array.length ? Math.max(...array) : 1;
			  vm.mobilePagesNum = maxBottom > 1 ? Math.ceil(maxBottom / vm.screenHeight) : 1;
			  console.log(vm.mobilePagesNum);
		  }
	  }, 1000);


		// vm.screenWidth = document.documentElement.clientWidth;
		// vm.screenHeight = document.documentElement.clientHeight;
		/**
			 * 1、pc
			 *  宽高使用pc宽高，
			 *  但是得确定多少尺寸的时候是手机屏幕
			 * 2、phone
			 *  根据bootstrap确定屏幕
			 *  <768 超小屏幕手机
			 *    限定范围<768
			 *  >=768平板
			 *    限定范围>=768 &&< 992平板范围
			 *  >=992桌面显示器
			 *    限定范围>=992 &&< 1200
			 *  >=1200桌面显示器
			 *    限定范围 同上 两种pc尺寸都兼容
			 * */
		// if (vm.screenWidth > 0 && vm.screenWidth < 768) {//此处判断写在mouted，methods html不管用
		// 	vm.differenceScreen = false;//phone
		// } else if (vm.screenWidth >= 768 && vm.screenWidth < 992) {//平板
		// 	vm.differenceScreen = true;
		// } else if (vm.screenWidth >= 992 && vm.screenWidth <= 1200) {
		// 	vm.differenceScreen = true;
		// } else {
		// 	vm.differenceScreen = true;
		// 	console.log('%c next', 'background: red;color: #fff;border-radius: 2px');
		// }
	}
};
</script>
<style scoped>
  .wrapper {
    overflow: hidden;
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-size: cover !important;
  }

  .wrapper.mobileLayout {
    overflow: scroll;
  }
  .wrapper.overflowY {
    padding-top: 40px;
  }

  .wrapper /deep/ .draggable {
    position: absolute;
    float: left;
    transform-origin: 0 0;
    background-color: transparent;
    color: transparent;
    border: solid 0.0625vw transparent;
  }

  .wrapper /deep/ .handle {
    z-index: 999;
    /* background-color: #28b3fc; */
    border: none;
    width: 0.3vw;
    height: 0.3vw;
  }

  /* .wrapper /deep/ .draggable.active {
    border-color: #28b3fc;
  } */

  .wrapper /deep/ .handle-tm {
    top: -0.15vw;
    margin-left: -0.15vw;
  }

  .wrapper /deep/ .handle-bm {
    bottom: -0.15vw;
    margin-left: -0.15vw;
  }

  .wrapper /deep/ .handle-tr {
    top: -0.15vw;
    right: -0.15vw;
  }

  .wrapper /deep/ .handle-bl {
    bottom: -0.15vw;
    left: -0.15vw;
  }

  .wrapper /deep/ .handle-br {
    bottom: -0.15vw;
    right: -0.15vw;
  }

  .wrapper /deep/ .handle-tl {
    top: -0.15vw;
    left: -0.15vw;
  }

  .wrapper /deep/ .handle-mr {
    right: -0.15vw;
    margin-top: -0.15vw;
  }

  .wrapper /deep/ .handle-ml {
    left: -0.15vw;
    margin-top: -0.15vw;
  }

  .wrapper /deep/ .container {
    /*overflow: hidden;*/
    /*position: absolute;*/
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .wrapper /deep/ .bg {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
  }

  .wrapper /deep/ .moreOption {
    position: absolute;
    top: 0.3vw;
    right: 0.6vw;
  }

  .wrapper /deep/ .moreOption:hover {
    opacity: 1;
  }

  .wrapper /deep/ .dataDimension {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .wrapper /deep/ .container .echarts {
    /*top: 0;*/
    /*left: 0;*/
    width: 100%;
    height: 100%;
  }

  .component-container {
    position: absolute;
    left: 0;
    top: 0;
  }
</style>

